  .post-page-title{
    color:$white-color;
    @include fluid-type($min_width, $max_width, 42px, 82px);
    text-transform:uppercase;
    margin-bottom:20px;
  }
  .blog-sec-title{
    display:flex;
    align-items:center;
    margin-bottom:18px;
    h4{
      color:#FFB81B;
      flex-shrink:0;
      margin-bottom:0;
      padding-right:15px;
      @include fluid-type($min_width, $max_width, 22px, 27px);
    }
    &::after{
      content:'';
      height:1px;
      background-color:#FFB81B;
      flex-grow:1;
      display:block;
    }    
  }
  .blog-list-block {
    background:$white-color;
    padding:24px 30px;
    height:calc(100% - 22px);
    margin-bottom:22px;
    border-radius:12px;
    box-shadow:0px 3px 6px #00000029;
    position:relative;
    display:flex;
    justify-content:space-between;
    flex-direction:column;
    @include media-breakpoint-down(lg){
      padding:14px;
    }
    h5{
      font-size:19.5px;
      margin-bottom:16px;
    }
  }
  .load-more-post{
    display:table;
    text-decoration:underline;
    margin:0px auto 35px;
    font-weight:500;
    &:hover{
      color:$primary-color;
      cursor:pointer;
    }
  }
}

.single-post-block{
  background-color:$white-color;
  padding:38px 0px 100px; 
  margin:110px 0px 70px;
  @include media-breakpoint-down(lg){
    margin:30px 0px 76px;
    padding:28px 15px 26px;   
  }
  box-shadow:0px 30px 60px #00000029;
  .single-post-title{
    text-align:center;
    padding-bottom:40px;
    margin-bottom:38px;
    border-bottom:1px solid #707070;
    h1{
      text-transform:uppercase;
      line-height:1.12;
      margin-bottom:36px;
      @include fluid-type($min_width, $max_width, 22px, 44px);
    }
    .post-behalf-of-cat{
      a{
        color:$primary-color;
      }
    }
  }
}

.related-posts{  
  @include media-breakpoint-up(xl){
    margin-bottom:100px;
  }
  h3{
    text-align:center;
    margin-bottom:32px;
  }
}
.post-pagination {
  justify-content:space-between;
  display:flex;
  margin:0px auto 100px;
  width:250px;
  align-items:center;
  img{
    width:9px;
    margin-top:-3px;
  }
  &.btn-prev{
    margin-right:5px;
  }
  &.btn-next{
    margin-left:5px;
  }
}